import {Registry} from 'cs-web-components-base';
import {prefixNS} from './helpers';
import {combineReducers} from 'cs-web-components-externals';
import {collections, channels, subscriptions, links} from './reducers/reducers';
import App from './containers/App';
import PostingList from './components/PostingList';
import ObjectActivities from './components/ObjectActivities';
import ObjectActivitiesBlock from './components/ObjectActivitiesBlock';
import DashboardWidget from './dashboard_widget/DashboardWidget.jsx';

Registry.registerComponent(prefixNS('App'), App);
Registry.registerComponent(prefixNS('PostingList'), PostingList);
Registry.registerComponent(prefixNS('ObjectActivities'), ObjectActivities);
Registry.registerComponent(prefixNS('ObjectActivitiesBlock'), ObjectActivitiesBlock);
Registry.registerComponent(prefixNS('DashboardWidget'), DashboardWidget);
Registry.registerReducer(
    componentNameSpace,
    combineReducers({collections, channels, subscriptions, links}));

export default {
    App,
    PostingList,
    ObjectActivities,
    ObjectActivitiesBlock
};



